import {
  getDetails,
  getRecommendations,
  getCredits,
  getProviders,
  getReviews
} from '@/lib/tmdb'
import DetailsClient from './DetailsClient'

export default async function DetailsPage({ params }) {
  const { type, id } = params

  const [data, recommendations, castData, providers, reviews] = await Promise.all([
    getDetails(type, id),
    getRecommendations(type, id),
    getCredits(type, id),
    getProviders(type, id),
    getReviews(type, id)
  ])

  return (
    <DetailsClient
      type={type}
      id={id}
      data={data}
      recommendations={recommendations}
      castData={castData}
      providers={providers}
      reviews={reviews}
    />
  )
}
